.PromptCloseButton {
    position: absolute;
    top: 5px;
    right: 5px;
    background-color: transparent;
    box-shadow: none;
    border-style: solid;
}
.promptInput {
    width: 100%;
    border: 0;
    padding: 5px;
    margin-bottom: 10px;
    margin-top: 10px;
    border-bottom: 1px solid #ccc;
}
.promptInput:focus {
    outline-width: 0;
}
.promptButton {
    padding: 15px;
    border-radius: 100px;
    flex-grow: 1;
    border: 0;
    background-color: #66cc66;
    color: white;
    margin-left: 10px;
}
.promptCancelButton {
    padding: 15px;
    border-radius: 100px;
    border: 0;
    background-color: #ff6666;
    color: white;
}
.promptButtonRow {
    max-width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-around;
    flex-direction: row-reverse;
}